import React, { useReducer } from "react";
import Head from "next/head";
import Header from "../components/header";
import GameContainer from "../components/gameContainer";
import { ScoreContext } from "../lib/ScoreContext";

const Home = () => {
  const [state, dispatch] = useReducer(
    (state, action) => {
      console.log(`action: ${action}`);
      switch (action) {
        case "score++":
          console.log("In score++");
          return {
            score: state.score + 1,
            highScore: state.highScore,
            win: state.win
          };
        case "setHigh":
          return { score: state.score, highScore: state.score, win: state.win };
        case "setWin":
          return { score: 0, highScore: state.score, win: true };
        case "setLoss":
          return { score: 0, highScore: state.highScore, win: false };
      }
    },
    {
      highScore: 0,
      score: 0,
      win: false
    }
  );

  return (
    <div>
      <Head>
        <title>LotR Clicker</title>
        <link
          rel="apple-touch-icon"
          sizes="180x180"
          href="/apple-touch-icon.png"
        />
        <link
          rel="icon"
          type="image/png"
          sizes="32x32"
          href="/favicon-32x32.png"
        />
        <link
          rel="icon"
          type="image/png"
          sizes="16x16"
          href="/favicon-16x16.png"
        />
        <link rel="manifest" href="/site.webmanifest" />
      </Head>
      <ScoreContext.Provider value={state}>
        <Header />

        <GameContainer dispatch={dispatch} />
      </ScoreContext.Provider>
      <style jsx global>{`
        body {
          margin: 0 auto;
        }
      `}</style>
    </div>
  );
};

export default Home;
